module.exports = "import React, { useState } from 'react';\nimport {\n  InputGroup,\n  InputGroupAddon,\n  InputGroupButtonDropdown,\n  Input,\n  Button,\n  DropdownToggle,\n  DropdownMenu,\n  DropdownItem\n } from 'reactstrap';\n\nconst Example = (props) => {\n  const [dropdownOpen, setDropdownOpen] = useState(false);\n  const [splitButtonOpen, setSplitButtonOpen] = useState(false);\n\n  const toggleDropDown = () => setDropdownOpen(!dropdownOpen);\n\n  const toggleSplit = () => setSplitButtonOpen(!splitButtonOpen);\n\n  return (\n    <div>\n      <InputGroup>\n        <InputGroupAddon addonType=\"prepend\"><Button>I'm a button</Button></InputGroupAddon>\n        <Input />\n      </InputGroup>\n      <br />\n      <InputGroup>\n        <Input />\n        <InputGroupButtonDropdown addonType=\"append\" isOpen={dropdownOpen} toggle={toggleDropDown}>\n          <DropdownToggle caret>\n            Button Dropdown\n          </DropdownToggle>\n          <DropdownMenu>\n            <DropdownItem header>Header</DropdownItem>\n            <DropdownItem disabled>Action</DropdownItem>\n            <DropdownItem>Another Action</DropdownItem>\n            <DropdownItem divider />\n            <DropdownItem>Another Action</DropdownItem>\n          </DropdownMenu>\n        </InputGroupButtonDropdown>\n      </InputGroup>\n      <br />\n      <InputGroup>\n        <InputGroupButtonDropdown addonType=\"prepend\" isOpen={splitButtonOpen} toggle={toggleSplit}>\n          <Button outline>Split Button</Button>\n          <DropdownToggle split outline />\n          <DropdownMenu>\n            <DropdownItem header>Header</DropdownItem>\n            <DropdownItem disabled>Action</DropdownItem>\n            <DropdownItem>Another Action</DropdownItem>\n            <DropdownItem divider />\n            <DropdownItem>Another Action</DropdownItem>\n          </DropdownMenu>\n        </InputGroupButtonDropdown>\n        <Input placeholder=\"and...\" />\n        <InputGroupAddon addonType=\"append\"><Button color=\"secondary\">I'm a button</Button></InputGroupAddon>\n      </InputGroup>\n    </div>\n  );\n}\n\n\nexport default Example;\n"