module.exports = "import React from 'react';\nimport { Button, ButtonGroup, ButtonToolbar } from 'reactstrap';\n\nconst Example = (props) => {\n  return (\n    <ButtonToolbar>\n      <ButtonGroup>\n        <Button>1</Button>\n        <Button>2</Button>\n        <Button>3</Button>\n        <Button>4</Button>\n      </ButtonGroup>\n      <ButtonGroup>\n        <Button>5</Button>\n        <Button>6</Button>\n        <Button>7</Button>\n      </ButtonGroup>\n      <ButtonGroup>\n        <Button>8</Button>\n      </ButtonGroup>\n    </ButtonToolbar>\n  );\n}\n\nexport default Example;\n"