module.exports = "import React from 'react';\nimport { Pagination, PaginationItem, PaginationLink } from 'reactstrap';\n\nconst Example = (props) => {\n  return (\n    <Pagination aria-label=\"Page navigation example\">\n      <PaginationItem disabled>\n        <PaginationLink first href=\"#\" />\n      </PaginationItem>\n      <PaginationItem disabled>\n        <PaginationLink previous href=\"#\" />\n      </PaginationItem>\n      <PaginationItem active>\n        <PaginationLink href=\"#\">\n          1\n        </PaginationLink>\n      </PaginationItem>\n      <PaginationItem>\n        <PaginationLink href=\"#\">\n          2\n        </PaginationLink>\n      </PaginationItem>\n      <PaginationItem>\n        <PaginationLink href=\"#\">\n          3\n        </PaginationLink>\n      </PaginationItem>\n      <PaginationItem>\n        <PaginationLink href=\"#\">\n          4\n        </PaginationLink>\n      </PaginationItem>\n      <PaginationItem>\n        <PaginationLink href=\"#\">\n          5\n        </PaginationLink>\n      </PaginationItem>\n      <PaginationItem>\n        <PaginationLink next href=\"#\" />\n      </PaginationItem>\n      <PaginationItem>\n        <PaginationLink last href=\"#\" />\n      </PaginationItem>\n    </Pagination>\n  );\n}\n\nexport default Example;\n"