module.exports = "import React from 'react';\nimport { Form, FormGroup, Label, Input } from 'reactstrap';\n\nconst Example = (props) => {\n  return (\n    <Form>\n      <FormGroup check inline>\n        <Label check>\n          <Input type=\"checkbox\" /> Some input\n        </Label>\n      </FormGroup>\n      <FormGroup check inline>\n        <Label check>\n           <Input type=\"checkbox\" /> Some other input\n        </Label>\n      </FormGroup>\n    </Form>\n  );\n}\n\nexport default Example;\n "